<template>
  <LeftRightLayout>
    <template v-slot:leftLayout>
      <div
        v-for="(item, index) in lists"
        :key="index"
        class="vue-menu"
        :class="{ 'is-active': activeMenu === item.name }"
      >
        <router-link :to="{ name: item.name }">
          {{ item.meta.title }}
        </router-link>
      </div>
    </template>
    <template v-slot:rightLayout>
      <router-view></router-view>
    </template>
  </LeftRightLayout>
</template>
<script>
import menus from '@/router/vue.js'
export default {
  components: {
    LeftRightLayout: () => import('@/components/leftRightLayout/index.vue')
  },
  watch: {
    $route (to) {
      this.activeMenu = to?.name || ''
    }
  },
  data () {
    const lists = menus.length > 0 && menus[0].children ? menus[0].children : []
    return {
      lists,
      activeMenu: ''
    }
  }
}
</script>
<style lang="less" scoped>
.vue-menu {
  height: 28px;
  line-height: 28px;
  padding: 0 10px;
  a {
    text-decoration: none;
    color: #333;
  }
  &:hover {
    a {
      text-decoration: underline;
    }
  }
  &.is-active a{
    color: #409eff;
  }
}
</style>